import React from 'react'

export const Navbar = () => {
    return (
        <nav className="navbar fixed-top" style={{background: "rgba(255, 255, 255, 0.19)", borderRadius: "0",
        boxShadow: "0 4px 30px rgba(0, 0, 0, 0.1)",
        backdropFilter: "blur(5px)",
        WebkitBackdropFilter: "blur(5px)",
        }}>
            <div className="container-fluid" style={{justifyContent: "center"}}>
                <span className="navbar-brand mb-0 h1">MOVIES SEARCH APP</span>
            </div>
        </nav>
    )
}
