import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Modal from '../Modal/Modal';
import { CircularProgressWithLabel } from '../Vote/Vote';

interface Props {
  id: number;
  title: string;
  poster_path: string;
  overview: string;
  vote_average: string;
}

export const Card: React.FC<Props> = ({ id, title, poster_path, overview, vote_average }) => {
  const [showModal, setShowModal] = useState(false);

  if (overview === "") {
    overview = "Nessuna descrizione";
  }
  const shortTitle = overview.slice(0, 45) + "...";

  const shortOverview = overview.slice(0, 80) + "...";

  const handleShowModal = () => {
    setShowModal(true);
  }

  const handleCloseModal = () => {
    setShowModal(false);
  }

  return (
    <div className="card" style={{ width: "20rem", borderRadius: "2rem", paddingTop: "0.7rem", margin: "1rem" }} key={id}>
      {poster_path ? (
        <img src={`https://image.tmdb.org/t/p/w500/${poster_path}`} alt={title} className="card-img-top" style={{ borderRadius: "1.5rem" }} />
      ) : (
        <img src="https://raw.githubusercontent.com/stefanosallemi/TMDB-APP/main/src/components/Card/default.webp" alt="Copertina non disponibile" className="card-img-top" style={{ borderRadius: "1.5rem" }} />
      )}
      <div className="card-body">
        <h5 className="card-title">{shortTitle}</h5>
        <p className="card-text">
          {shortOverview}
        </p>
        <div className='row' style={{ bottom: "0" }}>
          <button className="btn btn-primary rounded-pill col" onClick={handleShowModal}>
            Mostra
          </button>
          <button className="btn rounded-pill" style={{marginRight: "-1rem", marginBottom: "-1rem", marginTop: "-0.8rem", width:"5rem", borderColor: "transparent"}} onClick={handleShowModal}>
          <CircularProgressWithLabel value={parseInt(vote_average)*10} />
          </button>
        </div>
        {showModal && <Modal onClose={handleCloseModal} show={true} />}
      </div>
    </div>
  )
}
